@import url(https://use.fontawesome.com/releases/v5.0.10/css/all.css);
.Structure {
  display: flex;
  flex-direction: column;
  flex: 1 1 100%;
  min-width: 0; }
  .Structure--horizontal {
    flex-direction: row; }

.MainHeader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1500; }
  .MainHeader #master-nav {
    display: none; }
    @media (min-width: 992px) {
      .MainHeader #master-nav {
        display: flex; } }
  .MainHeader #master-menu {
    display: flex; }
    @media (min-width: 992px) {
      .MainHeader #master-menu {
        display: none; } }

.MainContent {
  padding-top: 5em; }


.Title, .fr-DocsArticle-heading > .fr-DocsArticle-title {
  font-weight: 700; }

.Subtitle, .fr-DocsArticle-heading > .fr-DocsArticle-subtitle {
  font-weight: 400; }

.fr-Docs {
  display: flex;
  flex-direction: row; }

.fr-DocsContent {
  position: relative;
  background-color: #FFFFFF;
  width: 100%;
  padding: 0;
  padding-top: 1.5rem;
  z-index: 500; }
  @media (min-width: 768px) {
    .fr-DocsContent {
      padding-top: 3rem; } }

@media (min-width: 768px) {
  .fr-DocsArticle {
    padding-left: 3rem;
    padding-right: 3rem; } }

.fr-DocsArticle-heading {
  margin-bottom: 2rem; }
  .fr-DocsArticle-heading > .fr-DocsArticle-subtitle {
    color: #6D7A7D; }

.fr-DocsArticle .Heading {
  margin-top: 3rem;
  margin-bottom: 1rem; }

.fr-DocsSidebar {
  display: none;
  position: -webkit-sticky;
  position: sticky;
  height: calc(100vh - 5em);
  top: 5em;
  padding-top: 1.5rem;
  padding-right: 1.5rem; }
  @media (min-width: 992px) {
    .fr-DocsSidebar {
      display: flex;
      flex: 0 0 300px; } }
  .fr-DocsSidebar-overlay {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 50%;
    background-color: #f2f4fb;
    z-index: 250; }

.fr-DocsMenu {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  z-index: 500; }
  .fr-DocsMenu-list {
    list-style: none;
    margin: 0;
    padding: 0; }
  .fr-DocsMenu-item > .fr-DocsMenu-link {
    display: block;
    color: inherit;
    font-weight: 700;
    padding: 0.8rem 1rem;
    transition: all 150ms cubic-bezier(0.78, 0.23, 0.33, 0.83); }
  .fr-DocsMenu-item--isActive > .fr-DocsMenu-link {
    color: #4061C4; }
  .fr-DocsMenu-item--hasSubmenu .fr-DocsMenu-item > .fr-DocsMenu-link {
    font-weight: 400;
    color: #6D7A7D; }
  .fr-DocsMenu-item--hasSubmenu .fr-DocsMenu-item--isActive > .fr-DocsMenu-link {
    background-color: #d9dff3;
    color: #4061C4;
    font-weight: 600;
    border-radius: 4px; }

.fr-AlertShowcase .Alert:not(:last-of-type) {
  margin-bottom: spacing(base); }

.Buttons {
  margin-bottom: -1rem; }
  .Buttons > .Button {
    margin-right: 1rem;
    margin-bottom: 1rem; }

.fr-card-demo {
  padding-top: 1rem;
  padding-bottom: 1rem; }
  .fr-card-demo .card {
    max-width: 400px; }

.fr-NavbarExample:not(:last-child) {
  margin-bottom: 1rem; }

.fr-NavbarExample__logo {
  font-weight: 700;
  font-size: 1.25rem;
  padding: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  border-radius: 4px; }

.fr-GridExample#grid-sizes .Grid:nth-child(2n+2) > span {
  background-color: #fae6d7;
  color: #6D7A7D; }

.fr-GridExample .Grids:not(:last-child) {
  margin-bottom: 1rem; }

.fr-GridExample .Grid > span {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-weight: 600;
  background-color: #eda774;
  color: #f0f2f2;
  padding-top: 1rem;
  padding-bottom: 1rem;
  border-radius: 4px; }
  .fr-GridExample .Grid > span > code {
    background-color: #e99457;
    color: #f0f2f2; }

.fr-GridExample .Grid:first-of-type {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px; }

.fr-GridExample .Grid:last-of-type {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px; }

.fr-Structure-showcase {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  position: relative;
  overflow: hidden;
  border-radius: 10px; }
  .fr-Structure-showcase .Structure {
    position: relative;
    background-color: #E58139; }
    .fr-Structure-showcase .Structure .Header,
    .fr-Structure-showcase .Structure .Content,
    .fr-Structure-showcase .Structure .Footer,
    .fr-Structure-showcase .Structure .Aside {
      display: flex;
      align-items: center;
      justify-content: center;
      letter-spacing: 0.5px;
      font-size: 87.5%;
      text-transform: uppercase;
      font-weight: 600; }
    .fr-Structure-showcase .Structure .Header,
    .fr-Structure-showcase .Structure .Footer {
      background-color: #ea9a61;
      color: #f0f2f2;
      height: 5em; }
    .fr-Structure-showcase .Structure .Content {
      flex: 1 1;
      background-color: #e58139;
      color: #f0f2f2;
      height: 15em; }
    .fr-Structure-showcase .Structure .Aside {
      flex: 0 0 200px;
      background-color: #e88e4d;
      color: #f0f2f2; }

.fr-Structure-example .Structure .Header .Navbar__container {
  padding-left: 2rem;
  padding-right: 2rem; }

.fr-Structure-example .Structure .Header #logo {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 600; }

.fr-Structure-example .Structure .Content {
  background-color: #f0f2f2;
  padding: 1rem; }
  .fr-Structure-example .Structure .Content-box {
    min-height: 20em;
    padding: 1rem;
    background-color: #FFFFFF;
    border-radius: 4px; }


