$navbar-transition: $transition !default;
$navbar-background: $white !default;
$navbar-color: color-yiq($navbar-background) !default;
$navbar-hovered-color: $primary-color;
$navbar-height: 5em;
$navbar-heights: () !default;
$navbar-heights: map-merge((
	xsmall: ($navbar-height * .5),
	small: ($navbar-height * .75),
	base: ($navbar-height * 1),
	large: ($navbar-height * 1.5),
	xlarge: ($navbar-height * 2)
), $navbar-heights);